const html = '\
<div  id=\'write\'  class = \'\'><h1><a name="bom" class="md-header-anchor"></a><span>BOM</span></h1><h2><a name="location" class="md-header-anchor"></a><span>location</span></h2><ol><li><code>location.href</code><span> -- 返回或设置当前文档的URL，</span><a href=\'http://localhost:8080/xhtml/?id=1&amp;name=1\' target=\'_blank\' class=\'url\'>http://localhost:8080/xhtml/?id=1&name=1</a></li><li><code>location.protocol</code><span> -- 返回URL中的协议部分，</span><code>http:</code></li><li><code>location.host</code><span> -- 返回URL中的域名部分，</span><code>localhost</code></li><li><code>location.port</code><span> -- 返回URL中的端口部分，</span><code>8080</code></li><li><code>location.hash</code><span> -- 返回URL#后面的内容，如果没有则返回空</span></li><li><code>location.pathname</code><span> -- 返回URL的域名后的部分。</span><code>/xhtml/</code></li><li><code>location.search</code><span> -- 返回URL中的查询字符串部分。</span><code>?id=1&amp;name=1</code></li><li><code>location.reload()</code><span> -- 重载当前页面 </span></li></ol><h2><a name="history" class="md-header-anchor"></a><span>history</span></h2><ol><li><code>history.go()</code><span> -- 前进或后退指定的页面数 history.go(num);</span></li><li><code>history.back()</code><span>-- 后退一页</span></li><li><code>history.forward()</code><span> -- 前进一页 </span></li></ol><h2><a name="navigator" class="md-header-anchor"></a><span>navigator</span></h2><ol><li><code>navigator.userAgent</code><span> -- 返回浏览器信息</span></li></ol></div>\
'; export default html