const html = '\
<div  id=\'write\'  class = \'\'><h1><a name="动画" class="md-header-anchor"></a><span>动画</span></h1><h2><a name="animate" class="md-header-anchor"></a><span>animate</span></h2><ul><li><p><span>指定的dom进行运动</span></p></li><li><p><span>参数</span></p><figure><table><thead><tr><th><span>值类型</span></th><th><span>说明</span></th></tr></thead><tbody><tr><td><span>string | {}</span></td><td><span>规定产生动画效果的一个或多个 CSS 属性 / 值，</span><em><span>必须是驼峰式写法</span></em></td></tr><tr><td><span>number | string</span></td><td><span>规定动画的速度。</span><br><span>可能的值：</span><br><span>-   毫秒</span><br><span>- “slow”</span><br><span>- “fase”</span></td></tr><tr><td><span>string</span></td><td><span>规定在动画的不同点中元素的速度。</span><br><span>- “swing&quot; - 在开头/结尾移动慢，在中间移动快</span><br><span>-“linear&quot; - 匀速移动</span></td></tr><tr><td><span>function</span></td><td><span>animate 函数执行完之后，要执行的函数。</span></td></tr></tbody></table></figure></li></ul><h2><a name="stop" class="md-header-anchor"></a><span>stop</span></h2><ul><li><p><span>阻止动画进行</span></p></li><li><p><span>参数</span></p><figure><table><thead><tr><th><span>值类型</span></th><th><span>说明</span></th></tr></thead><tbody><tr><td><span>Boolean</span></td><td><span>是否停止后续所有运动</span><br><span>- false：不停止</span><br><span>- true：停止后续所有运动</span></td></tr><tr><td><span>Boolean</span></td><td><span>是否立即到达当前目标点</span><br><span>- false：不到达</span><br><span>- true：到达</span></td></tr></tbody></table></figure></li></ul><h2><a name="切换" class="md-header-anchor"></a><span>切换</span></h2><h4><a name="slideup" class="md-header-anchor"></a><span>slideUp</span></h4><ul><li><p><span>如果dom已显示出来了，则通过滑动的效果隐藏 dom</span></p></li><li><p><span>参数</span></p><figure><table><thead><tr><th><span>值类型</span></th><th><span>说明</span></th></tr></thead><tbody><tr><td><span>number</span></td><td><span>滑动过程执行的总时间</span></td></tr><tr><td><span>function</span></td><td><span>回调函数</span></td></tr></tbody></table></figure></li></ul><p>&nbsp;</p><h4><a name="slidedown" class="md-header-anchor"></a><span>slideDown</span></h4><ul><li><p><span>如果dom没显示，则通过滑动的效果显示出来</span></p></li><li><p><span>参数</span></p><figure><table><thead><tr><th><span>值类型</span></th><th><span>说明</span></th></tr></thead><tbody><tr><td><span>number</span></td><td><span>滑动过程执行的总时间</span></td></tr><tr><td><span>function</span></td><td><span>回调函数</span></td></tr></tbody></table></figure></li></ul><p>&nbsp;</p><h4><a name="slidetoggle" class="md-header-anchor"></a><span>slideToggle()</span></h4><ul><li><p><span>通过滑动来切换dom的可见状态（slideUp和slideDown的切换）</span></p></li><li><p><span>参数</span></p><figure><table><thead><tr><th><span>值类型</span></th><th><span>说明</span></th></tr></thead><tbody><tr><td><span>number</span></td><td><span>滑动过程执行的总时间</span></td></tr><tr><td><span>function</span></td><td><span>回调函数</span></td></tr></tbody></table></figure></li></ul><p>&nbsp;</p><h4><a name="fadein" class="md-header-anchor"></a><span>fadeIn()</span></h4><ul><li><p><span>如果dom没显示，则通过渐显的效果显示出来</span></p></li><li><p><span>参数</span></p><figure><table><thead><tr><th><span>值类型</span></th><th><span>说明</span></th></tr></thead><tbody><tr><td><span>number</span></td><td><span>渐显过程执行的总时间</span></td></tr><tr><td><span>function</span></td><td><span>回调函数</span></td></tr></tbody></table></figure></li></ul><p>&nbsp;</p><h4><a name="fadeout" class="md-header-anchor"></a><span>fadeOut()</span></h4><ul><li><p><span>如果dom已显示出来了，则通过渐隐的效果隐藏dom</span></p></li><li><p><span>参数</span></p><figure><table><thead><tr><th><span>值类型</span></th><th><span>说明</span></th></tr></thead><tbody><tr><td><span>number</span></td><td><span>渐隐过程执行的总时间</span></td></tr><tr><td><span>function</span></td><td><span>回调函数</span></td></tr></tbody></table></figure></li></ul><p>&nbsp;</p><h4><a name="fadetoggle" class="md-header-anchor"></a><span>fadeToggle()</span></h4><ul><li><p><span>如果dom已显示出来了，则通过渐隐的效果隐藏dom</span></p></li><li><p><span>参数</span></p><figure><table><thead><tr><th><span>值类型</span></th><th><span>说明</span></th></tr></thead><tbody><tr><td><span>number</span></td><td><span>渐隐渐显过程执行的总时间</span></td></tr><tr><td><span>function</span></td><td><span>回调函数</span></td></tr></tbody></table></figure></li></ul><p>&nbsp;</p></div>\
'; export default html