import React from 'react'
import { withRouter } from 'react-router-dom'
import './Article.scss'
import '../config/style.css'

const Article = ({ match }) => {
    const id = match.params.id
    const articleHtml = require(`../articles/${id}.js`)
    return (
        <div className="article-container">
            <div dangerouslySetInnerHTML={{ __html: articleHtml.default }} />
            <footer className="footer">
                <ul>
                    <li>本文为博主原创文章，转载请附上原文出处链接和本声明。 </li>
                    <li>如有疑问，请发送邮件至 543657931@qq.com!</li>
                </ul>
            </footer>
        </div>
    )
}

export default withRouter(Article)