const html = '\
<div  id=\'write\'  class = \'\'><h1><a name="生命周期" class="md-header-anchor"></a><span>生命周期</span></h1><p><img src="https://cdn.jsdelivr.net/gh/kingmusi/blogImages/img/批注 2020-07-24 171717.png" referrerpolicy="no-referrer"></p><h2><a name="初始化阶段" class="md-header-anchor"></a><span>初始化阶段</span></h2><ol start=\'\' ><li><code>super(props)</code><span>，将父组件的props传给给子组件</span></li><li><code>constructor()</code><span>，用来做一些组件的初始化工作，如定义this.state的初始内容</span></li></ol><h2><a name="挂载阶段" class="md-header-anchor"></a><span>挂载阶段</span></h2><ol start=\'\' ><li><code>componentWillMount</code><span> —— 在</span><code>组件被挂载到页面之前调用</code><span>，只调用一次</span></li><li><span>render</span></li><li><code>componentDidMount</code><span> —— 在</span><code>组件已经被挂载到页面后调用</code><span>，只调用一次</span></li></ol><blockquote><p><strong><span>多组件</span></strong></p><p><code>Parent constructor</code><span>  =&gt;  </span><code>Parent render</code><span>  =&gt;  </span><code>Child constructor</code><span>  =&gt;  </span><code>Child render</code><span>  =&gt;  </span><code>Child componentDidMount</code><span>  =&gt;  </span><code>Parent componentDidMount</code></p></blockquote><h2><a name="更新阶段" class="md-header-anchor"></a><span>更新阶段</span></h2><ol start=\'\' ><li><p><code>shouldComponentUpdate</code><span> —— 在</span><code>组件被更新之前调用</code></p><ul><li><span>需要返回一个</span><code>布尔值</code></li><li><span>为</span><code>true</code><span>，则</span><code>继续往下执行</code><span>更新阶段的生命周期函数</span></li><li><span>为</span><code>false</code><span>，则</span><code>不执行</code><span>更新阶段的生命周期函数</span></li></ul></li><li><p><code>componentWillUpdate</code><span> —— </span><code>在 shouldComponentUpdate 返回 true 后执行</code></p></li><li><p><span>render</span></p></li><li><p><code>componentDidUpdate</code><span> —— </span><code>组件更新完成后执行</code></p></li></ol><blockquote><p><strong><span>多组件</span></strong></p><p><code>Parent shouldComponentUpdate</code><span> =&gt;  </span><code>Parent render</code><span>  =&gt;  </span><code>Child shouldComponentUpdate</code><span>  =&gt;  </span><code>Child render</code><span>  =&gt;  </span><code>Child componetDidUpdate</code><span>  =&gt; </span><code>Parent componetDidUpdate</code></p></blockquote><h2><a name="挂载阶段-n40" class="md-header-anchor"></a><span>挂载阶段</span></h2><ol start=\'\' ><li><code>componentWIllUnmount</code><span> —— 在</span><code>这个组件被剔除之前调用</code></li></ol><blockquote><p><strong><span>多组件</span></strong></p><p><code>Child componentWillUnmount</code><span>  =&gt;  </span><code>Parent componentWillUnmoun</code></p></blockquote></div>\
'; export default html