const html = '\
<div  id=\'write\'  class = \'\'><h1><a name="mvvm" class="md-header-anchor"></a><span>MVVM</span></h1><h4><a name="传统组件" class="md-header-anchor"></a><span>传统组件</span></h4><ol start=\'\' ><li><span>静态渲染</span></li><li><span>更新依赖于操作</span><code>DOM</code></li></ol><h4><a name="数据驱动视图" class="md-header-anchor"></a><span>数据驱动视图</span></h4><ol start=\'\' ><li><span>M（Model）</span></li><li><span>V（View）</span></li><li><span>VM（ViewModel）</span></li></ol><p><img src="https://cdn.jsdelivr.net/gh/kingmusi/blogImages/img/746387-20170223155932085-1172851114.png" referrerpolicy="no-referrer"></p><ul><li><span>数据(</span><code>Model</code><span>)和视图(</span><code>View</code><span>)是不能直接通讯的，而是需要通过</span><code>ViewModel</code><span>来实现双方的通讯。</span></li><li><span>当数据变化的时候，</span><code>viewModel</code><span>能够监听到这种变化，并及时的通知</span><code>view</code><span>做出修改。</span></li><li><span>当页面有事件触发时，</span><code>viewModel</code><span>也能够监听到事件，并通知</span><code>model</code><span>进行响应。</span></li><li><code>Viewmodel</code><span>就相当于一个观察者，监控着双方的动作，并及时通知对方进行相应的操作</span></li></ul></div>\
'; export default html