const html = '\
<div  id=\'write\'  class = \'\'><h1><a name="js的各种宽高" class="md-header-anchor"></a><span>js的各种宽高</span></h1><h2><a name="宽高" class="md-header-anchor"></a><span>宽高</span></h2><ol><li><p><code>clientHeight</code><span>、</span><code>clientWidth</code><span>→元素宽高（</span><code>height+padding</code><span>，不包含边框）</span></p></li><li><p><code>offsetHeight</code><span>、</span><code>offectWidth</code><span>→元素宽高（</span><code>height+padding+border</code><span>，包含边框）</span></p></li><li><p><code>scrollHeight</code><span>、</span><code>scrollWidth</code><span>→元素宽高（</span><mark><span>内容</span></mark><span>的实际高度+上下</span><code>padding</code><span>）</span></p><ul><li><span>内容的理解：就是如果一个box里面的文字内容超出了box的高，即溢出了，溢出的部分也要算上</span></li></ul></li></ol><h2><a name="滚动距离" class="md-header-anchor"></a><span>滚动距离</span></h2><ol><li><code>offsetTop</code><span>：为容器相对于</span><code>document</code><span>的</span><code>top</code><span>的绝对偏移，等于</span><code>top+margin-top</code></li><li><code>clientTop</code><span>:   容器内部相对于容器本身的</span><code>top</code><span>偏移，实际就是</span><code>border-width</code></li><li><code>scrollTop</code><span>：滚动了的距离（没有滚动条的时候为 </span><code>0</code><span>）</span></li></ol></div>\
'; export default html